body {
  background: #000;
  font-family: "Poppins", sans-serif;
}
.home {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 50px;
  /* background: url(); background-size: cover; USE THIS FOR PICTURE AS BACKGROUND*/
  display: flex;
  align-items: center;
  justify-content: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.content {
  width: 100%;
}
.content h2 {
  color: #fff;
  font-size: 2.5em;
}
#name {
  color: #40c3ff;
  font-size: 1.2em;
}
.content .buttons {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.cvButton {
  display: flex;
  align-items: center;
  justify-content: center;
}

#wrap {
  border-right: 0.08em solid #fff;
}
.typewrite {
  text-align: center;
}

.line-1 {
  position: relative;
  top: 50%;
  margin: 0 auto;
  border-right: 2px solid rgba(255, 255, 255, 0.75);
  white-space: nowrap;
  overflow: hidden;
  transform: translateY(-50%);
}

/* Animation */
.anim-typewriter {
  -webkit-animation: blinkTextCursor 500ms steps(44) infinite normal;
          animation: blinkTextCursor 500ms steps(44) infinite normal;
}
@-webkit-keyframes blinkTextCursor {
  from {
    border-right-color: rgba(255, 255, 255, 0.75);
  }
  to {
    border-right-color: transparent;
  }
}
@keyframes blinkTextCursor {
  from {
    border-right-color: rgba(255, 255, 255, 0.75);
  }
  to {
    border-right-color: transparent;
  }
}

@media (max-width: 750px) {
  .content h2 {
    font-size: 1.5em;
  }

  .content .buttons,
  .cvButton {
    width: 100%;
  }
  .logoText {
    display: none;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
:root {
  --main-color: coral;
}
.containerButton {
  display: flex;
}
#neonButton {
  position: relative;
  padding: 10px 30px;
  margin: 20px 30px 30px 30px;
  color: coral;
  color: var(--main-color);
  text-decoration: none;
  /* text-transform: uppercase; */
  letter-spacing: 2px;
  font-size: 20px;
  overflow: hidden;
  transition: 0.5s;
  -webkit-box-reflect: below 1px linear-gradient(transparent, #0003);
}
#neonButton:hover {
  background: coral;
  background: var(--main-color);
  color: #111;
  box-shadow: 0 0 50px coral;
  box-shadow: 0 0 50px var(--main-color);
  transition-delay: 0.3s;
}
#neonButton:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  border-top: 2px solid coral;
  border-top: 2px solid var(--main-color);
  border-left: 2px solid coral;
  border-left: 2px solid var(--main-color);
  transition: 0.5s;
  transition-delay: 0.2s;
}
#neonButton:hover:before {
  width: 100%;
  height: 100%;
}
#neonButton:after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid coral;
  border-bottom: 2px solid var(--main-color);
  border-right: 2px solid coral;
  border-right: 2px solid var(--main-color);
  transition: 0.5s;
  transition-delay: 0.2s;
}
#neonButton:hover:after {
  width: 100%;
  height: 100%;
}

@media (max-width: 750px) {
  #neonButton {
    padding: 5px 20px;
    margin: 10px 15px 15px 15px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
#contactContainer {
  min-height: 100vh;
  padding: 15%;
}
#contactContainer h2 {
  width: 100%;
  color: #45f3ff;
  font-size: 36px;
  text-align: center;
  margin-bottom: 10px;
}

#contactContainer .row100 {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

#contactContainer .row100 .col {
  position: relative;
  width: 100%;
  padding: 0 10px;
  margin: 30px 0 10px;
  transition: 0.5s;
}

#contactContainer .row100 .inputBox {
  position: relative;
  width: 100%;
  height: 40px;
  color: #45f3ff;
}

#contactContainer .row100 .inputBox input,
#contactContainer .row100 .inputBox.textarea textarea {
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  box-shadow: none;
  border: none;
  outline: none;
  font-size: 18px;
  padding: 0 10px;
  z-index: 1;
  color: #000;
}
#contactContainer .row100 .inputBox .text {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 40px;
  font-size: 18px;
  padding: 0 10px;
  display: block;
  transition: 0.5s;
  pointer-events: none;
}
#contactContainer .row100 .inputBox input:focus + .text,
#contactContainer .row100 .inputBox input:valid + .text {
  top: -35px;
  left: -10px;
}

#contactContainer .row100 .inputBox .line {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 2px;
  background: #45f3ff;
  transition: 0.5s;
  border-radius: 2px;
  pointer-events: none;
}
#contactContainer .row100 .inputBox input:focus ~ .line,
#contactContainer .row100 .inputBox input:valid ~ .line {
  height: 100%;
}
#contactContainer .row100 .inputBox.textarea {
  position: relative;
  width: 100%;
  height: 100px;
  padding: 10px 0;
}
#contactContainer .row100 .inputBox.textarea textarea {
  height: 100%;
  resize: none;
}
#contactContainer .row100 .inputBox textarea:focus + .text,
#contactContainer .row100 .inputBox textarea:valid + .text {
  top: -35px;
  left: -10px;
}
#contactContainer .row100 .inputBox textarea:focus ~ .line,
#contactContainer .row100 .inputBox textarea:valid ~ .line {
  height: 100%;
}
.sendButton {
  display: flex;
  justify-content: flex-end;
}

